<template>
  <div id="topic">
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif" alt="">
    </div>
    <div v-else>
      <div id="topic-panel">
        <div class="topic-header">
          <h1 class="topic-title">{{topic.title}}</h1>
          <ul class="topic-info">
            <li> {{topic.create_at | formatDate}}</li>
            <li> {{topic.author.loginname}}</li>
            <li>{{topic.visit_count}} </li>
            <li> {{topic.tab | formatTab}}</li>
          </ul>
          
        </div>
        <!-- markdown -->
        <div v-html="topic.content" id='content' class="topic-content ">
        </div>
      </div>
      <div id="reply-pannel" v-if="topic.reply_count > 0">
        <div class="reply-header">
          <span>{{ topic.reply_count }} </span>
        </div>
        <div class="reply-content">
          <ul>
            <li v-for="(reply,index) in topic.replies" class="reply">

              <router-link :to="{name:'user',params:{
                username:reply.author.loginname
              }}"><img :src="reply.author.avatar_url" ></router-link>
              
              <div class="cell">
                <span class="reply-author">{{reply.author.loginname}}</span>
                <span class="reply-info">{{ index+1 }}{{reply.create_at | formatDate}}</span>
                
                <!-- <span v-if="reply.ups.length>0">
                  {{reply.ups.length}}
                </span>
                <span class="reply-time"></span>
                <span v-if="topic.author.loginname == reply.author.loginname">
                  
                </span> -->
                <div id="content " class="reply-word" v-html="reply.content"></div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    
    </div>
  </div>
</template>

<script>
export default {
  name: 'Article',
  data () {
    return {
      isLoading:true,
      topic: {}
    }
  },
  methods: {
    getTopic(){
      this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`).then(res=>{
        // this.$route.params.id router
        //console.log(res.data)
         
        this.isLoading = false
        this.topic = res.data.data
      }).catch(function(err){
        console.log(err)
      })
    }
  },
  beforeMount(){
    
    this.getTopic()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

  @import url('../assets/markdown-github.css');
  /* topic */
  #topic {
    width: 80%;
    margin: 0 auto;
    background-color: #e1e1e1;
  }
  #topic-panel {
    
    background-color: #fff;
    border-radius: 3px;
    
    margin-bottom: 20px ;
  }
  #reply-pannel {
    /* margin-top: 20px; */
    border-radius: 3px;
    background-color: #fff;
   
  }
  .topic-header {
    padding: 10px;
  }
  .topic-content {
    margin: 0 10px;
    border-top: 1px solid #e5e5e5;
    padding: 10px;
  }
  
  /* topic-heder */
   .topic-title {
     font-size: 22px;
     margin: 8px 0;
   }
   .topic-info {
      font-size: 12px;
      color: #838383;
   }
   .topic-info li {
     float: left;
     margin-left: 20px;
   }
   .topic-info::after {
     content: '';
     display: block;
     clear: both;

   }
   /* reply-pannel */
  .reply-header {
    padding: 10px;
    background: #f6f6f6;
    border-radius: 3px 3px 0 0;
  }
  .reply-header span {
    color: #444;
  }
  .reply-content {
    position: relative;
     
  }
  .reply-content img {
    width: 30px;
    height: 30px;
    border-radius: 3px;
    position: absolute;
    cursor: pointer;
    
  }
  .reply-content .reply {
    border: 1px solid #f0f0f0;
    border-bottom: none;
    padding: 20px 10px 40px;
  }
  .reply-content .reply:last-child{
    border-bottom: 1px solid #f0f0f0;
  }
  .reply .cell {
    margin-left: 40px;
  }
  .reply .reply-author{
    color: #666;
    font-weight: 700;
  }
  .reply-info {
    font-size : 11px;
    color : #08c;
  }
  .reply-word {
    margin-top: 5px;
  }
</style>



